.l {
  padding: 0 percentage(70/1200) 0 percentage(245/1200);
  @include r(1024) {
    padding-left: 40px;
    padding-right: 40px; }
  @include r(767) {
    padding-left: 15px;
    padding-right: 15px; } }

.row {
  margin: 0 -30px;
  @include clr; }

.col {
  float: left;
  box-sizing: border-box;
  padding: 0 30px;
  width: 100%; }

$col-sizes: 80, 75, 60, 50, 33, 25, 20;
@each $size in $col-sizes {
  .col-#{$size} {
    width: #{$size}%; } }
